* {
  margin:0;
  padding:0;
  box-sizing: content-box;
}
body{
  font-family: 'montserrat', sans-serif;
  background-color: #091c2e;
}
@media (min-width: 1381px) {
  header {
    padding: 50px 15px 0px;
  }
  header h1 {
    color:#FFF;
    font-size: 48px;
    font-weight: 500;
    text-align: center;
  }
  .searchbox-wrap {
    width:100%;
    max-width: 960px;
    margin: 0 auto;
  }
  .searchbox{
    display: block;
    width:100%;
    padding: 15px;

    border: none;
    outline: none;
    background: none;

    background-color: #EEE;
    border-radius: 8px;

    color:#53565A;
    font-size: 20px;
    font-weight: 300;

    transition: 0.4s ease-out;
  }
  .searchbox:hover {
    box-shadow: 0px 0px 30px 3px #4484C4;
  }
  .searchbox:focus {
    box-shadow: 0px 0px 30px 3px #4484C4;
  }
  .clear {
    color:rgb(92, 203, 246);
    text-decoration: underline;
    cursor: pointer;
    margin-bottom:15px;
  }
  .clear:hover {
    color: #FFF;
    text-decoration: underline;
    cursor: pointer;
  }
  .nav {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
  }
  .nav-list-item {
    float:left;
  }
  .nav-list-item:hover {
    cursor: pointer;
  }
  .clicked {
    float:left;
    background-color: #111;
    cursor: pointer;
  }
  .unclicked {
    float:left;
    background-color: #333;
    cursor: pointer;
  }
  .nav li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
  .nav li a:hover {
    background-color: #111;
  }
  .results {
    display: flex;
    flex-wrap: wrap;
  }
  .results .result {
    min-width:250px;
    max-width:250px;
    padding:10px;
    display: flex;
    flex-direction: column;
    cursor: pointer;
  }
  .results .result img {
    display:block;
    width:250px;
    min-height: 400px;
    max-height: 400px;
  }
  .results .result h3 {
    color: #FFF;
    font-size: 20px;
    font-weight: 600;
    padding: 10px;
    background: #445565;
    flex: 1 1 100%;
    transition: 0.4s ease-out;
  }
  .results .result:hover h3 {
    background: #FFF;
    color: #223343;
  }
  .popup {
    position:fixed;
    top:0;
    left:0;
    width:100vw;
    height:100vh;
    background-color:#0c0d0e8f;
    overflow-y: scroll;
  }
  .popup .content {
    display: block;
    width: 100%;
    max-width: 960px;
    padding: 25px;
    position:absolute;
    left:50%;
    top:50%;
    transform: translate(-50%, -50%);
    border: 3px dotted black;
    background-color:#0c0d0ee1;
    color: #FFF;
  }
  .popup .content h2 {
    font-size: 28px;
    font-weight: 600;
  }
  .popup .content h2 span {
    font-size: 20px;
    font-weight: 300;
  }
  .popup .content .rating {
    margin-bottom: 30px;
    color:orange;
  }
  .popup .content .close {
    display: inline-block;
    padding: 15px 30px;
    font-size: 18px;
    font-weight: 700;

    background: #4484C4;
    color: #FFF;
    border-radius: 8px;
    border: none;
    outline: none;
    appearance: none;
    cursor: pointer;

    transition: 0.4s ease-out;
  }
  .popup .content .close:hover {
    background: #223343;
  }

  .flexbox-item img{
    width:250px;
  }

  .flexbox-container {
    display:flex;
    justify-content:flex-start;
  }
  .flexbox-info {
    padding-left:15px;
    padding-right:15px;
  }
}
